@import '../../styles/sass/variables.scss';

.title{
    position: relative;
    & h2{
        margin: 0 0 60px;
        font-weight: 300;
        color: $textColor;
        text-transform: capitalize;
        font-size: 36px;
        line-height: 1.2;
        letter-spacing: 5px;
    }
    &::after{
        position: absolute;
        content: "";
        background: $primaryColor;
        width: 55px;
        height: 1px;
        right: 50%;
        transform: translate(50%,50%);
        bottom: 30px;
    }
}