@import '../common/styles/sass/variables.scss';

.nav{
    width: 300px;
    display: flex;
    justify-content: space-between;
    & a{
        text-decoration: none;
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        &:hover{
            color: $primaryColor;
        }
    }
    .active{
        color: $primaryColor;
    } 
}