@import '../../common/styles/sass/variables.scss';
@import '../../common/styles/sass/mixins.scss';

.project {
    max-width: 450px;
    background: $bgSecondColor;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .icon {
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        &:hover::before {
            position: absolute;
            content: '';
            background-color: black;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            opacity: 0.5;
        }

        &:hover .viewBtn {
            display: block;
        }

        .viewBtn {
            position: relative;
            z-index: 1;
            display: none;
            @media only screen and (max-width: 1024px) {
                display: block;
                border-radius: 4px;
                padding: 10px 20px;
                background-color: $primaryColor;
            }
        }
    }

    .info {
        padding: 25px 20px;

        .title {
            @include text (16px, 1.2);
            letter-spacing: 1px;
            margin: 0 0 5px;
        }

        .description {
            @include text (12px, 1.8);
            font-weight: 400;
            font-family: 'Poppins', sans-serif;
        }
    }
}