@import '../common/styles/sass/variables.scss';

.footer{
    height: 150px;
    background-color: $bgSecondColor;
    color: #fff;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 30px 0;
    .container{
        flex-direction: column;
        .list {
            width: 330px;
            display: flex;
            justify-content: space-between;
            & a{
                color: #fff;
            }
        }
    }
}
