.btn {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 20px;
    letter-spacing: 1px;
    padding: 7px 20px;
    cursor: pointer;
    transition: all 0.4s ease-in-out 0s;
    background: transparent;
    color: #fff;
    max-width: 170px;
    margin: 15px auto;
    z-index: 3;
    text-align: center;
    text-decoration: none;
    border-radius: 0px;
    width: 100%;
    border: none;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 10px;
        width: 50%;
        background: #4e93e6;
        z-index: -1;
        transition: all 0.2s ease-in-out 0s;
        margin: 0 auto;
    }
    &:hover::after {
        height: 100%;
        width: 100%;
    }
}